import { useState } from 'react'
import { useStore } from '../App'
import Question from '../components/question'
import questions from '../data/questions'

const SectionTwo = () => {
  const section = 1
  const sectionQuestions = questions[section]
  const [showQuestion, setShowQuestion] = useState([])
  // const requiredQuestions = useStore((state) => state.requiredQuestions)

  const setSection2Answers = useStore((state) => state.setSection2Answers)
  const section2Answers = useStore((state) => state.section2Answers)

  const handleChange = (questionIndex) => (item) => {
    setSection2Answers(
      {
        item: item.selectedItem,
        key: sectionQuestions[questionIndex].key,
      },
      questionIndex
    )
    if (item.selectedItem.showQuestion) {
      showQuestion.push(item.selectedItem.showQuestion)
      setShowQuestion([...showQuestion])
    }
  }

  const handleOtherTextChange = (questionIndex) => (ev) => {
    const text = ev.target.value

    setSection2Answers(
      {
        item: { ...section2Answers[questionIndex].item, otherText: text },
      },
      questionIndex
    )
  }

  return (
    <section>
      {sectionQuestions.map(({ key, ...question }, questionIndex) => {
        return (
          <Question
            {...question}
            key={`${key}-${questionIndex}`}
            subText="Sagitiis amet, neque, tortor enim sem."
            questionIndex={questionIndex}
            section={section}
            handleChange={handleChange(questionIndex)}
            handleOther={handleOtherTextChange(questionIndex)}
            showQuestion={
              showQuestion.includes(questionIndex) ? questionIndex : -1
            }
            showOther={
              section2Answers[questionIndex] &&
              section2Answers[questionIndex].item.showOtherText
            }
          />
        )
      })}
    </section>
  )
}

export default SectionTwo
