import Button from '../components/button'
import { useStore } from '../App'
import mapAnswersToPostString from '../utils/mapAnswersToPostString'
// import findUndefined from '../utils/findUndefined'

const SectionTwoButtons = ({ setVisibleSection, visibleSection }) => {
  const section1Answers = useStore((state) => state.section1Answers)
  const section2Answers = useStore((state) => state.section2Answers)
  // const setRequiredQuestions = useStore((state) => state.setRequiredQuestions)
  // const requiredQuestions = useStore((state) => state.requiredQuestions)

  return (
    <>
      <Button
        icon="previous"
        variant="outline"
        onClick={() => {
          setVisibleSection(visibleSection - 1)
        }}
      >
        Back
      </Button>
      <Button
        icon="next"
        onClick={async () => {
          const postString = mapAnswersToPostString([
            ...section1Answers,
            ...section2Answers,
          ])
          console.log('POST body ->', postString)
          const url = 'https://webtools.ec.europa.eu/form-tools/process.php'
          const response = await fetch(url, {
            method: 'POST',
            mode: 'no-cors',
            headers: {
              'Access-Control-Allow-Origin': '*',
              'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
            },
            body: postString,
          })
          console.log('response from the post ->', response)
          setVisibleSection(visibleSection + 1)
        }}
      >
        Submit
      </Button>
    </>
  )
}

export default SectionTwoButtons
