import DropdownSelect from '../dropdown'
import OtherTextInput from '../otherText'
import RadioGroup from '../radio-group'

const Question = ({
  hidden,
  showQuestion,
  questionIndex,
  type,
  section,
  question,
  options,
  handleChange,
  showOther,
  handleOther,
  required,
}) => {
  if (hidden && showQuestion !== questionIndex) {
    return null
  }

  return (
    <div className={'question-wrapper'}>
      {type === 'radio' ? (
        <RadioGroup
          question={question}
          items={options}
          handleChange={handleChange}
        />
      ) : (
        <DropdownSelect
          id={questionIndex}
          section={section}
          question={question}
          subText="Sagitiis amet, neque, tortor enim sem."
          items={options}
          handleChange={handleChange}
          required={required}
        />
      )}
      {showOther && (
        <OtherTextInput
          id={`${section}-${questionIndex}-other`}
          handleChange={handleOther}
        />
      )}
    </div>
  )
}

export default Question
