import { useState } from 'react'
import { useStore } from '../App'
import questions from '../data/questions'
import Question from '../components/question'

const SectionOne = () => {
  const section = 0
  const section1Questions = questions[section]
  const setSection1Answers = useStore((state) => state.setSection1Answers)
  const requiredQuestions = useStore((state) => state.requiredQuestions)
  const [otherQuestionsToShow, setOtherQuestionsToShow] = useState([])

  const handleChange = (questionIndex) => (item) => {
    if (item) {
      // checks to see if we need to capture other text and whether the user has selected 'Other'
      const isOtherSelected =
        section1Questions[questionIndex].captureOtherText &&
        item.selectedItem.showOtherText

      if (isOtherSelected) {
        // user has selected Other so push this question into the tracking array so the text box will appear
        setOtherQuestionsToShow([...otherQuestionsToShow, questionIndex])
      } else {
        // user hasn't selected Other however if Other was selected previously then check and remove it so that the box disappears
        // TODO: may need to remove the answer as well to clear everything
        if (otherQuestionsToShow.includes(questionIndex)) {
          setOtherQuestionsToShow(
            otherQuestionsToShow.filter((ind) => ind !== questionIndex)
          )
        }
      }

      setSection1Answers(
        { item: item.selectedItem, key: section1Questions[questionIndex].key },
        questionIndex
      )
    }
  }

  const handleOtherTextChange = (questionIndex) => (ev) => {
    const text = ev.target.value
    setSection1Answers(
      { item: text, key: section1Questions[questionIndex].key + '-otherText' },
      questionIndex
    )
  }

  // use the trackingArray to keep a track of fields that need other text
  const showOtherTextField = (trackingArray, questionIndex) =>
    trackingArray.filter((q) => q === questionIndex).length > 0

  return (
    <section>
      {section1Questions.map((question, questionIndex) => (
        <Question
          {...question}
          key={`question-wrapper-${questionIndex}`}
          questionIndex={questionIndex}
          subText="Sagitiis amet, neque, tortor enim sem."
          handleChange={handleChange(questionIndex)}
          showOther={showOtherTextField(otherQuestionsToShow, questionIndex)}
          handleOther={handleOtherTextChange(questionIndex)}
          required={
            requiredQuestions.filter((rq) => rq === questionIndex)[0] ===
            questionIndex
          }
        />
      ))}
    </section>
  )
}

export default SectionOne
