import styles from './radio-group.module.css'

const Radio = ({ value, name, handleChange, item }) => {
  return (
    <>
      <input
        className={styles.radioInput}
        type="radio"
        onChange={() => handleChange({ selectedItem: item })}
        id={`${name}-${value}-radio`}
        name={name}
      />
      <label htmlFor={`${name}-${value}-radio`} className={styles.radioLabel}>
        {value}{' '}
      </label>
    </>
  )
}

const RadioGroup = ({ items, question, handleChange }) => {
  return (
    <fieldset className={styles.fieldset}>
      <legend className={styles.legend}>{question}</legend>
      {items.map((item, itemIndex) => {
        const value = typeof item === 'string' ? item : item.text

        return (
          <Radio
            key={`${question}-${itemIndex}`}
            name={question}
            value={value}
            handleChange={handleChange}
            item={item}
          />
        )
      })}
    </fieldset>
  )
}

export default RadioGroup
