import styles from './otherText.module.css'

const OtherTextInput = ({ handleChange, value, id }) => {
  return (
    <div className={styles.otherQuestionWrapper}>
      <label htmlFor={id} className={styles.label}>
        Please specify
      </label>
      <input
        id={id}
        onChange={handleChange}
        className={styles.input}
        type="text"
        value={value}
      />
    </div>
  )
}

export default OtherTextInput
